'use server';
/**
 * @fileOverview An AI agent for converting PDF files to editable Word documents.
 *
 * - convertPdfToWord - A function that handles the PDF to Word conversion.
 * - ConvertPdfToWordInput - The input type for the function.
 * - ConvertPdfToWordOutput - The return type for the function.
 */

import {ai} from '@/ai/genkit';
import {googleAI} from '@genkit-ai/googleai';
import {z} from 'genkit';

const ConvertPdfToWordInputSchema = z.object({
  pdfDataUri: z
    .string()
    .describe(
      "A PDF file, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:application/pdf;base64,<encoded_data>'"
    ),
  model: z
    .string()
    .optional()
    .describe('The AI model to use for conversion.'),
});
export type ConvertPdfToWordInput = z.infer<typeof ConvertPdfToWordInputSchema>;

const ConvertPdfToWordOutputSchema = z.object({
  docxDataUri: z.string().describe('The converted Word document as a data URI.'),
});
export type ConvertPdfToWordOutput = z.infer<typeof ConvertPdfToWordOutputSchema>;

export async function convertPdfToWord(
  input: ConvertPdfToWordInput
): Promise<ConvertPdfToWordOutput> {
  return pdfToWordFlow(input);
}

const prompt = ai.definePrompt({
  name: 'pdfToWordPrompt',
  input: {schema: ConvertPdfToWordInputSchema},
  output: {schema: ConvertPdfToWordOutputSchema},
  prompt: `You are an expert at converting PDF documents into perfectly formatted Microsoft Word (.docx) files.
  Convert the provided PDF file into a .docx file.
  Preserve the original layout, formatting, text, images, and tables as accurately as possible.
  The output must be a valid .docx file encoded as a data URI.

  PDF File: {{media url=pdfDataUri}}`,
  model: 'googleai/gemini-1.5-pro-latest',
  config: {
    // Requesting the specific DOCX mime type for the output
    responseMimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
  }
});

const pdfToWordFlow = ai.defineFlow(
  {
    name: 'pdfToWordFlow',
    inputSchema: ConvertPdfToWordInputSchema,
    outputSchema: ConvertPdfToWordOutputSchema,
  },
  async input => {
    const model = input.model ? googleAI.model(input.model) : undefined;
    const response = await prompt(input, {model});
    const output = response.output();
    
    if (!output || !output.docxDataUri) {
      throw new Error(
        'PDF to Word conversion failed: The model did not return a valid document.'
      );
    }
    return output;
  }
);
