export function PdfToWordPage() {
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [isConverting, setIsConverting] = useState(false);
  const [convertedDocxUri, setConvertedDocxUri] = useState('');
  const [isDragging, setIsDragging] = useState(false);
  const [language, setLanguage] = useState<'km' | 'en'>('en');
  const [isSupportSheetOpen, setIsSupportSheetOpen] = useState(false);
  const [selectedModel, setSelectedModel] = useState('gemini-1.5-pro-latest');

  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const t = useMemo(() => translations[language], [language]);
  
  const resetState = () => {
    setPdfFile(null);
    setConvertedDocxUri('');
  }

  const handleFileSelect = useCallback(async (file: File | null | undefined) => {
    if (!file) return;

    if (file.type !== 'application/pdf') {
      toast({
        title: t.invalidFileType,
        description: t.selectPdfFile,
        variant: "destructive",
      });
      return;
    }
    
    resetState();
    setPdfFile(file);
    setIsConverting(true);

    try {
      const pdfDataUri = await blobToBase64(file);
      const result = await convertPdfToWord({ pdfDataUri, model: selectedModel });

      if (result && result.docxDataUri) {
        setConvertedDocxUri(result.docxDataUri);
      } else {
        toast({
          title: t.conversionFailed,
          description: t.noText,
          variant: "destructive",
        });
      }
    } catch (e: any) {
      console.error(e);
      let title = t.conversionError;
      let description = e.message || "An error occurred while processing your PDF.";
      const errorMessage = (e.message || '').toLowerCase();
      if (errorMessage.includes('429') || errorMessage.includes('rate limit')) {
          title = t.rateLimitExceeded;
          description = t.rateLimitMessage;
      }
      toast({
        title: title,
        description: description,
        variant: "destructive",
      });
    } finally {
      setIsConverting(false);
    }
  }, [t, toast, selectedModel]);


  const handleDragEvents = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDragEnter = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    if (e.dataTransfer.items && e.dataTransfer.items.length > 0) {
      setIsDragging(true);
    }
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
  };
  
  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
    handleFileSelect(e.dataTransfer.files?.[0]);
  };
  
  const handleDownload = () => {
    const link = document.createElement('a');
    link.href = convertedDocxUri;
    link.download = pdfFile?.name.replace(/\.pdf$/i, '.docx') || 'converted_document.docx';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  const toggleLanguage = useCallback(() => {
    setLanguage(prevLang => prevLang === 'en' ? 'km' : 'en');
  }, []);

  const isReadyForContent = !isConverting && convertedDocxUri.length > 0;
  
  const modelDisplayNames = useMemo(() => ({
    'gemini-1.5-flash-latest': 'Gemini 1.5 Flash',
    'gemini-1.5-pro-latest': 'Gemini 1.5 Pro',
  }), []);
  
  const formatBytes = (bytes: number, decimals = 2) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
}


  return (
    <div className="flex flex-col h-screen bg-background text-foreground">
        <header className="flex-shrink-0 flex items-center justify-between p-4 border-b">
            <h1 className="text-xl font-bold">VoiceScribe</h1>
            <div className="hidden md:flex items-center gap-4">
                <Button variant="outline" asChild>
                    <Link href="/">{t.voiceScribe}</Link>
                </Button>
                <Button variant="outline" asChild>
                    <Link href="/pdf-transcript">{t.pdfTranscript}</Link>
                </Button>
                <Button variant="default" asChild>
                    <Link href="/pdf-to-word">{t.pdfToWord}</Link>
                </Button>
            </div>
            <div className="flex items-center gap-2">
                <Select value={selectedModel} onValueChange={setSelectedModel}>
                  <SelectTrigger className="w-[180px]">
                    <SelectValue placeholder={t.selectModel} />
                  </SelectTrigger>
                  <SelectContent>
                    {Object.entries(modelDisplayNames).map(([value, label]) => (
                      <SelectItem key={value} value={value}>{label}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                <Button variant="outline" size="icon" onClick={toggleLanguage}>
                    <Languages className="h-4 w-4" />
                </Button>

                <Sheet open={isSupportSheetOpen} onOpenChange={setIsSupportSheetOpen}>
                  <SheetTrigger asChild>
                    <Button variant="outline" size="icon">
                        <Sparkles className="h-4 w-4" />
                    </Button>
                  </SheetTrigger>
                  <SheetContent side="left">
                    <SheetHeader className="text-left">
                        <SheetTitle>{t.support}</SheetTitle>
                        <SheetDescription>
                            {t.supportDescription}
                        </SheetDescription>
                    </SheetHeader>
                    <div className="h-full w-full rounded-md border mt-4 overflow-hidden">
                      <iframe src="https://0zodesigner.github.io/donate/" className="w-full h-full" title="Donate" />
                    </div>
                  </SheetContent>
                </Sheet>
            </div>
        </header>

        <main 
            className="flex-grow p-4 md:p-6 grid grid-cols-1 gap-6 place-content-center bg-muted/40"
            onDragEnter={handleDragEnter}
            onDragOver={handleDragEvents}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
        >
            <input
                type="file"
                ref={fileInputRef}
                onChange={(e) => handleFileSelect(e.target.files?.[0])}
                className="hidden"
                accept="application/pdf"
            />
            
            {isConverting ? (
                 <Card className="w-full max-w-lg mx-auto">
                    <CardHeader className="text-center">
                        <Loader2 className="mx-auto h-12 w-12 animate-spin text-primary mb-4" />
                        <CardTitle>{t.converting}</CardTitle>
                    </CardHeader>
                    {pdfFile && (
                        <CardContent className="text-center text-muted-foreground">
                            <p>{pdfFile.name}</p>
                            <p>{formatBytes(pdfFile.size)}</p>
                        </CardContent>
                    )}
                 </Card>
            ) : isReadyForContent && pdfFile ? (
                 <Card className="w-full max-w-lg mx-auto text-center">
                    <CardHeader>
                       <FileText className="mx-auto h-12 w-12 text-green-500 mb-4" />
                       <CardTitle>{t.convertedSuccessfully}</CardTitle>
                       <CardDescription>{pdfFile.name}</CardDescription>
                    </CardHeader>
                    <CardContent>
                       <Button onClick={handleDownload} size="lg">
                           <Download className="mr-2" />
                           {t.download}
                       </Button>
                    </CardContent>
                 </Card>
            ) : (
                <div 
                    className={cn(
                        "w-full max-w-lg mx-auto flex flex-col items-center justify-center text-center rounded-lg border-2 border-dashed border-muted-foreground/20 bg-background h-64 transition-colors cursor-pointer",
                        isDragging && "border-primary bg-primary/10"
                    )}
                    onClick={() => fileInputRef.current?.click()}
                >
                    <FileUp className="w-20 h-20 text-muted-foreground/30 mb-4"/>
                    <h3 className="text-2xl font-semibold">{t.readyToConvert}</h3>
                    <p className="text-muted-foreground mt-2 mb-4">{t.dropPdf}</p>
                </div>
            )}
        </main>
    </div>
  );
}

    